/*
---
name: Heading h1
category:
 - component/typography/heading
---

## h1

```html
<h1 class="c-heading--lv1">h1テキストh1テキストh1テキストh1テキストh1テキストh1テキストh1テキストh1テキスト</h1>

<article class="c-article">
	<h1>h1テキストh1テキストh1テキストh1テキスト</h1>
</article>

```
*/

.c-heading--lv1 {
	color: $c_main;
	line-height: 1.2;
	@include pc() {
		margin-bottom: 30px;
		@include fz(40);
	}
	@include sp() {
		margin-bottom: 20px;
		@include fz(28);
	}
}

.c-article h1 {
	@extend .c-heading--lv1;
}

/*
---
name: Heading h2
category:
 - component/typography/heading
---

## h2

```html
<h2 class="c-heading--lv2">h2テキストh2テキストh2テキストh2テキストh2テキストh2テキストh2テキストh2テキスト</h2>

<article class="c-article">
	<h2>h2テキストh2テキストh2テキストh2テキスト</h2>
</article>

```
*/

.c-heading--lv2 {
	border-bottom: 2px solid $c_main;
	line-height: 1.4;
	@include pc() {
		margin-bottom: 30px;
		padding-bottom: 15px;
		@include fz(28);
	}
	@include sp() {
		margin-bottom: 20px;
		padding-bottom: 10px;
		@include fz(22);
	}
}

.c-article h2 {
	@extend .c-heading--lv2;
}


/*
