.c-heading--lv3 {
	background-color: $c_main;
	color: #fff;
	line-height: 1.4;
	@include pc() {
		margin-bottom: 20px;
		padding: 5px 10px;
		@include fz(22);
	}
	@include sp() {
		margin-bottom: 10px;
		padding: 5px 10px;
		@include fz(18);
	}
}

.c-article h3 {
	@extend .c-heading--lv3;
}


/*
---
name: Heading h4
category:
 - component/typography/heading
---

## h4

```html
<h4 class="c-heading--lv4">h4テキストh4テキストh4テキストh4テキストh4テキストh4テキストh4テキストh4テキスト</h4>

<article class="c-article">
	<h4>h4テキストh4テキストh4テキストh4テキスト</h4>
</article>

```
*/

.c-heading--lv4 {
	border-left: 4px solid $c_main;
	color: $c_main;
	line-height: 1.4;
	@include pc() {
		margin-bottom: 20px;
		padding: 3px 0 3px 10px;
		@include fz(18);
	}
	@include sp() {
		margin-bottom: 10px;
		padding: 2px 0 2px 10px;
		@include fz(16);
	}
}

.c-article h4 {
	@extend .c-heading--lv4;
}

