a:not([class]) {
	color: $c_main;
	text-decoration: underline;
	&:hover { text-decoration: none; }
}

.c-link__icon {
	@include fz(16);
	vertical-align: 0;
	// リンクの前に配置
	&.-front { margin-right: 3px; }
	// リンクの後ろに配置
	&.-rear { margin-left: 5px; }
}